import React, {useState} from "react";
import PropTypes from "prop-types";

//Functional Component
let CounterApp = ({value = 10}) =>{ //Deconstruye los argumentos que envia react

	//Hook recibe un valor y regresa un arreglo donde el primero es el valor y el segundo una funcion
	//Que actualiza el valor
	const [counter,setCounter] = useState(value); //Retorna un arreglo con 2 valores

	const handleAdd = () => {
		setCounter(counter + 1);
		//setCounter( (c) => {c + 1}); //Si no se tiene acceso directo a counter
	}

	const handleSubstract = () => {
		setCounter(counter - 1);
	}

	const handleReset = () => {
		setCounter(value);
	}

	return (
		<>
			<h1>CounterApp</h1>
			<h2>{counter}</h2>
			<button onClick= {handleAdd} >+1</button>
			<button onClick= {handleReset	}> Reset</button>
			<button onClick= {handleSubstract} >-1</button>
		</>
	);
}

CounterApp.propTypes = {
	value: PropTypes.number
}

export {
	CounterApp
}